@import url(https://fonts.googleapis.com/css?family=Ubuntu);

.center1 { 
  margin: auto;
  position: fixed;
  top: 0; left: 0; bottom: 0; right: 0; }

.loadContainer1 { height: 35px;
  width: 35px;
  float: left; }

.container1 { height: 35px;
width: 205px; }

.loading {  font: "Ubuntu";
font-size: 35px;
  font-style: oblique;
color: black;
padding-left: 16px;
  text-shadow: 0px 0px 4px #9CF;
animation: loadingText .4s ease-in-out infinite alternate;
}

.loader2 {
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  border-radius: 50%;
  height: 14px;
  width: 15px;
  margin: 10px;
  position: absolute; 
  animation: loading 1.2s infinite linear; }

.loader3 {
  border-left: 3px solid black;
  border-right: 3px solid black;
  border-radius: 50%;
  height: 24px;
  width: 25px;
  margin: 8px 2px;
  position: absolute;
  animation: loading 1s infinite linear; }

.loader4 {
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  border-radius: 50%;
  height: 34px;
  width: 35px;
  position: absolute; 
  animation: loading .8s infinite linear; }

@keyframes loading {
  from { transform: rotate(0deg); }
  to { transform: rotate(359deg); } }

@keyframes loadingText {
  to {text-shadow:
    0 0 2px #9CF,
    0 0 3px #9CF,
    0 0 4px #69C,
    0 0 5px #69C; } }