@import url(https://fonts.googleapis.com/css?family=Ubuntu);
.login-container{
    margin-top: 15%;
}
.login-form-1{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
}
.login-form-1 h3{
    text-align: center;
    color: #333;
}
.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
    background: #0062cc;
}
.login-form-2 h3{
    text-align: center;
    color: #fff;
}
.login-container form{
    padding: 10%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}
.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}
.login-form-2 .btnSubmit{
    background-color: #fff;
    color: #0062cc;
}
.login-form-2 .ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}
.login-form-1 .ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}
/* Modal */
.ReactModalPortal > div{
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    background: white;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 620px;
    max-width: 500px;
    outline: none;
    padding: 10px;
}

/* Datepicker */

.react-datetime-picker input:focus {
    outline: none;
}

.react-datetime-picker__wrapper {
    border: 0px solid gray !important;

}

/* FABS */
.fab {
    border-radius: 100%;
    bottom: 25px;
    font-size: 30px;
    padding: 25px;
    position: fixed;
    right: 25px;
}

.fab-danger {
    bottom: 25px;
    padding: 10px;
    position: fixed;
    left: 25px;
}


.center1 { 
  margin: auto;
  position: fixed;
  top: 0; left: 0; bottom: 0; right: 0; }

.loadContainer1 { height: 35px;
  width: 35px;
  float: left; }

.container1 { height: 35px;
width: 205px; }

.loading {  font: "Ubuntu";
font-size: 35px;
  font-style: oblique;
color: black;
padding-left: 16px;
  text-shadow: 0px 0px 4px #9CF;
-webkit-animation: loadingText .4s ease-in-out infinite alternate;
        animation: loadingText .4s ease-in-out infinite alternate;
}

.loader2 {
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  border-radius: 50%;
  height: 14px;
  width: 15px;
  margin: 10px;
  position: absolute; 
  -webkit-animation: loading 1.2s infinite linear; 
          animation: loading 1.2s infinite linear; }

.loader3 {
  border-left: 3px solid black;
  border-right: 3px solid black;
  border-radius: 50%;
  height: 24px;
  width: 25px;
  margin: 8px 2px;
  position: absolute;
  -webkit-animation: loading 1s infinite linear;
          animation: loading 1s infinite linear; }

.loader4 {
  border-top: 3px solid black;
  border-bottom: 3px solid black;
  border-radius: 50%;
  height: 34px;
  width: 35px;
  position: absolute; 
  -webkit-animation: loading .8s infinite linear; 
          animation: loading .8s infinite linear; }

@-webkit-keyframes loading {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(359deg); transform: rotate(359deg); } }

@keyframes loading {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(359deg); transform: rotate(359deg); } }

@-webkit-keyframes loadingText {
  to {text-shadow:
    0 0 2px #9CF,
    0 0 3px #9CF,
    0 0 4px #69C,
    0 0 5px #69C; } }

@keyframes loadingText {
  to {text-shadow:
    0 0 2px #9CF,
    0 0 3px #9CF,
    0 0 4px #69C,
    0 0 5px #69C; } }
